			// 	title: _this.detailList.compInfo.abbr+" 正在招聘 "+_this.detailList.name+" 【"+_this.detailList.salary+"】",
			// 	imageUrl:_this.tempFilePath,
			// 	path: '/components/jobDetail/jobDetail?shareId='+_this.shareId+'&id='+_this.id,
			// };
		}
	}
